import React from "react";
import "./ProfilePage.scss";

const ProfileComponent = (props) => {
  return (
    <div className="new">
      <div className="ProfileContainter">
        <div className="top">
          <h1>Profile</h1>
          <div className="left">
            <div className="itemImg-Profile">
              <img
                className="edit-img"
                src={
                  props.imageUrl
                    ? props.imageUrl
                    : "data:image/jpeg;base64,"
                }
                alt=""
              />
            </div>
          </div>
          <div className="right">
            <div className="info">
              <h2>Name: {props?.username?.toUpperCase().split("@")[0]}</h2>
              <p>Age: 30</p>
              <p>Location: New York</p>
              <p>Email: {props.email}</p>
              <p>Phone Number: {props.phone_number}</p>
            </div>
          </div>
        </div>
        <div className="bottom">
          <h2>About Me</h2>
          <p>
            Introducing {props?.username?.toUpperCase().split("@")[0]}, the
            mastermind behind unforgettable events. With a passion for
            creativity and a commitment to excellence,{" "}
            {props?.username?.toUpperCase().split("@")[0]} brings expertise and
            a personalized touch to every occasion. Meticulous planning, trend
            awareness, and adaptability define{" "}
            {props?.username?.toUpperCase().split("@")[0]}'s approach. They
            effortlessly orchestrate everything from corporate conferences to
            weddings, ensuring each event reflects the client's unique vision.{" "}
            {props?.username?.toUpperCase().split("@")[0]} excels in logistics,
            vendor management, and budgeting, while also infusing creativity
            into decor, entertainment, and branding. Professional, reliable, and
            dedicated to client satisfaction,{" "}
            {props?.username?.toUpperCase().split("@")[0]} fosters strong
            relationships and leverages an extensive network of trusted
            partners. Sit back and embrace the magic with{" "}
            {props?.username?.toUpperCase().split("@")[0]} as your event
            management guru.
          </p>
        </div>
      </div>
    </div>
  );
};

export default ProfileComponent;
